import React, { Component } from "react"
import * as firebase from "firebase"


let cliente
class FormNuevoCliente extends Component {
    state = {
        nombre: undefined,
        direccionPrincipal: undefined,
        direccionSecundaria: undefined,
        ciudad: undefined,
        codigoPostal: undefined,
        expreso: undefined,
        CUIT: undefined,
        DNI: undefined,
    }
    async submitForm() {
        let cliente = {
            [this.state.nombre]: {
                datos: {
                    nombre: this.state.nombre,
                    direccion: {
                        0: this.state.direccionPrincipal,
                        1: this.state.direccionSecundaria,
                    },
                    telefono: {
                        0: this.state.telefono1,
                        1: this.state.telefono2,
                        2: this.state.telefono3,
                        3: this.state.telefono4,
                    },
                    ciudad: this.state.ciudad + " CP:" + this.state.codigoPostal,
                    deuda: 0,
                    expreso: this.state.expreso,
                    CUIT: this.state.CUIT,
                    DNI: this.state.DNI
                }
            }
        }
        await firebase.database().ref().child("clientes2").update(cliente)
    }
    render() {
        return (
            <div className="container mt-4">
                <div className="row">
                    <div className="col-8 offset-2">
                        <form onSubmit={e => {
                            e.preventDefault()
                            this.submitForm()
                        }}>
                            <div className="form-row">
                                <div className="form-group col-12">
                                    <label>Nombre</label>
                                    <input type="text" value={this.state.nombre} onChange={e => {
                                        this.setState(
                                            {
                                                ...this.state,
                                                nombre: e.target.value
                                            }
                                        )
                                    }} className="form-control" id="nombre" />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-6">
                                    <label for="inputAddress">Direccion Principal</label>
                                    <input type="text" className="form-control" id="inputAddress" value={this.state.direccionPrincipal} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            direccionPrincipal: e.target.value
                                        })
                                    }} />
                                </div>
                                <div className="form-group col-6">
                                    <label for="inputAddress2">Direccion Secundaria</label>
                                    <input type="text" className="form-control" id="inputAddress2" value={this.state.direccionSecundaria} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            direccionSecundaria: e.target.value
                                        })
                                    }} />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-md-6">
                                    <label>Ciudad</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.ciudad} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            ciudad: e.target.value
                                        })
                                    }} />
                                </div>

                                <div className="form-group col-md-2">
                                    <label for="inputZip">Codigo Postal</label>
                                    <input type="text" className="form-control" id="inputZip" value={this.state.codigoPostal} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            codigoPostal: e.target.value
                                        })
                                    }} />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-md-12">
                                    <label>expreso</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.expreso} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            expreso: e.target.value
                                        })
                                    }} />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-md-3">
                                    <label>telefono 1</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.telefono1} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            telefono1: e.target.value
                                        })
                                    }} />
                                </div>
                                <div className="form-group col-md-3">
                                    <label>telefono 2</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.telefono2} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            telefono2: e.target.value
                                        })
                                    }} />
                                </div>
                                <div className="form-group col-md-3">
                                    <label>telefono 3</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.telefono3} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            telefono3: e.target.value
                                        })
                                    }} />
                                </div>
                                <div className="form-group col-md-3">
                                    <label>telefono 4</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.telefono4} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            telefono4: e.target.value
                                        })
                                    }} />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-md-6">
                                    <label>CUIT</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.CUIT} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            CUIT: e.target.value
                                        })
                                    }} />
                                </div>

                                <div className="form-group col-md-6">
                                    <label for="inputZip">DNI</label>
                                    <input type="text" className="form-control" id="inputZip" value={this.state.DNI} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            DNI: e.target.value
                                        })
                                    }} />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-12 text-center">
                                    <button type="submit" className="btn btn-primary">Agregar Cliente</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>

        )
    }
}
export default FormNuevoCliente